import React from 'react'
import { Mutation } from 'react-apollo'
import gql from 'graphql-tag'

const SIGNOUT_MUTATION = gql`
    mutation SIGNOUT_MUTATION{
        signout{
            message
        }
    }
`

const Signout = () => {
    return (
        <Mutation>
            <button>Sign Out</button>
        </Mutation>
    )
}

export default Signout
